<script>
  export default {
    created () {
    },
    computed: {
      articleDescription: {
        get () {
          return this.$store.state.setting['content.seo.article.description']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.article.description',
            value: value
          })
        }
      },
      articleKeyword: {
        get () {
          return this.$store.state.setting['content.seo.article.keyword']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.article.keyword',
            value: value
          })
        }
      },
      articleTitle: {
        get () {
          return this.$store.state.setting['content.seo.article.title']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.article.title',
            value: value
          })
        }
      },
      categoryDescription: {
        get () {
          return this.$store.state.setting['content.seo.category.description']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.category.description',
            value: value
          })
        }
      },
      categoryKeyword: {
        get () {
          return this.$store.state.setting['content.seo.category.keyword']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.category.keyword',
            value: value
          })
        }
      },
      categoryTitle: {
        get () {
          return this.$store.state.setting['content.seo.category.title']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.category.title',
            value: value
          })
        }
      },
      pageDescription: {
        get () {
          return this.$store.state.setting['content.seo.page.description']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.page.description',
            value: value
          })
        }
      },
      pageKeyword: {
        get () {
          return this.$store.state.setting['content.seo.page.keyword']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.page.keyword',
            value: value
          })
        }
      },
      pageTitle: {
        get () {
          return this.$store.state.setting['content.seo.page.title']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'content.seo.page.title',
            value: value
          })
        }
      }
    },
    methods: {
      submit: function (e) {
        this.$validator.validateAll()
        if (this.errors.any()) {
          return false
        }
      }
    },
    mounted () {
      this.$store.commit('title', 'SEO配置 - 文章 - Notadd Administration')
    }
  }
</script>
<template>
    <div class="box box-solid">
        <div class="box-header with-border">
            <h3 class="box-title">文章默认SEO配置</h3>
        </div>
        <div class="box-body">
            <div class="form-horizontal">
                <div class="form-group" :class="{ 'has-error': errors.has('articleTitle') }">
                    <label class="col-sm-1 control-label">标题</label>
                    <div class="col-sm-3">
                        <input name="articleTitle" type="text" class="form-control" placeholder="请输入标题" v-model="articleTitle" v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('articleTitle')">标题不能为空</span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('articleDescription') }">
                    <label class="col-sm-1 control-label">描述</label>
                    <div class="col-sm-3">
                        <input name="articleDescription" type="text" class="form-control" placeholder="请输入描述" v-model="articleDescription" v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('articleDescription')">描述不能为空</span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('articleKeyword') }">
                    <label class="col-sm-1 control-label">关键字</label>
