<script>
  import LayoutHeader from './LayoutHeader'

  export default {
    components: {
      LayoutHeader
    },
    data: () => {
      return {
        menu: [
          {
            'text': '基础配置',
            'icon': 'icon icon-home',
            'uri': '/'
          },
          {
            'text': '内容管理',
            'icon': 'icon icon-article',
            'uri': '/content'
          },
          {
            'text': '公众平台',
            'icon': 'icon icon-wechat',
            'uri': '/wechat'
          }
        ],
        nav: [
          {
            text: '全局设置',
            icon: 'fa fa-globe',
            uri: '/setting'
          },
          {
            text: '附件设置',
            icon: 'fa fa-cloud-upload',
            uri: '#',
            menu: [
              {
                text: '上传设置',
                icon: 'fa fa-file-o',
                uri: '/upload'
              },
              {
                text: '存储设置',
                icon: 'fa fa-cloud',
                uri: '/storage'
              },
              {
                text: '水印设置',
                icon: 'fa fa-circle-o',
                uri: '/watermark'
              },
              {
                text: 'CDN 设置',
                icon: 'fa fa-circle-o',
                uri: '/cdn'
              }
            ]
          },
          {
            text: 'SEO 设置',
            icon: 'fa fa-search',
            uri: '/seo'
          },
          {
            text: '邮件设置',
            icon: 'fa fa-envelope-o',
            uri: '/email',
            menu: [
              {
                text: '邮件设置',
                icon: 'fa fa-envelope-o',
                uri: '/email'
              }
            ]
          },
          {
            text: '调试工具',
            icon: 'fa fa-exclamation',
            uri: '/debug'
          }
        ]
      }
    }
  }
</script>
<template>
    <div class="wrapper">
        <layout-header :menu="menu"></layout-header>
        <router-view></router-view>
    </div>
</template>


